@font-face {
  font-family: 'FreightSansLFPro';
  src: url('assets/FreigSanLFProBoo.otf') format('opentype');
}

/* colors */
:root {
  --facebook: #4767ab;
  --instagram: #e11c54;
  --whatsapp: #20b038;
  --fb-ig: #944280;
  --fb-wa: #348c72;
  --ig-wa: #816646;
  --fb-ig-wa: #655c5e;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'FreightSansLFPro', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--oc-gray-9);
}

strong {
  font-weight: 600;
}

.hide {
  display: none;
}

.fb {
  color: var(--facebook);
}
.ig {
  color: var(--instagram);
}
.wa {
  color: var(--whatsapp);
}
