header {
  background-color: var(--oc-gray-1);
  -webkit-box-shadow: 0px 2px 2px 0px rgba(33,37,41,0.2);
  -moz-box-shadow: 0px 2px 2px 0px rgba(33,37,41,0.2);
  box-shadow: 0px 2px 2px 0px rgba(33,37,41,0.2);
  padding: 16px 0 12px;
  text-align: center;
  background-color: var(--oc-gray-8);
  color: white;
}
  header .title {
    font-size: 1.4em;
    font-weight: 800;
    letter-spacing: 0.1em;
    margin-bottom: 8px;
  }
  header .desc {
    border-top: 1px solid var(--oc-gray-7);
    display: inline-block;
    padding: 12px 8px 0;
  }
  .fb, .ig, .wa {
    font-weight: 600;
  }

footer {
  background-color: var(--oc-gray-8);
  color: white;
  padding: 24px 48px;
}
footer a {
  color: var(--oc-blue-4);
}

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.5);
}
  .popup-message {
    position: absolute;
    left: 25%;
    right: 25%;
    top: 25%;
    bottom: 25%;
    margin: auto;
    background: white;
    border-radius: 4px;
    border: 2px solid var(--oc-red-9);
    padding: 12px 20px 24px;
    line-height: 140%;
    height: min-content;
  }
  .popup-message a {
    color: var(--facebook);
  }
    .warning-icon {
      width: 36px;
      height: 36px;
      background: url('assets/warning.svg') no-repeat;
      margin-bottom: 4px;
    }
    .App .warning-icon {
      margin: 0 auto;
    }
    .confirm-wrapper {
      text-align: center;
    }
      .confirm-botton {
        padding: 12px 20px;
        background-color: var(--oc-gray-8);
        color: white;
        display: inline-block;
        font-weight: 600;
        margin: 0 auto;
        letter-spacing: 0.06em;
        border-radius: 8px;
      }
      .confirm-botton:hover {
        cursor: pointer;
        background-color: var(--oc-gray-4);
        color: var(--oc-gray-9);
      }
