.setting-vis {
  width: 75%;
  max-width: 100%;
  margin: 24px auto 0;
  display: flex;
  justify-content: center;
  margin-bottom: 24px;
  position: relative;
}
  .setting-vis.disabled {
    color: var(--oc-gray-6);
  }
  .setting-vis-item {
    padding: 12px 36px;
  }
  .setting-vis-item:not(:last-child) {
    border-right: 1px dashed var(--oc-gray-4);
  }
  .setting-vis-title {
    font-size: 18px;
    margin-bottom: 8px;
  }
  .setting-select-wrapper {
    margin-bottom: 8px;
  }
  .setting-select-wrapper .vis-option {
    line-height: 140%;
  }
  .setting-select-wrapper .vis-option-list:not(:last-child) {
    margin-bottom: 4px;
  }
  .setting-select-wrapper span.vis-option {
    margin-right: 8px;
  }
  .second-title {
    padding-top: 12px;
    margin-top: 12px;
    border-top: 1px solid var(--oc-gray-4);
  }

  .setting-vis-month {
    position: absolute;
    width: 200px;
    padding: 12px 12px;
    border-radius: 8px;
    background-color: var(--oc-gray-8);
    color: white;
    text-align: center;
    top: 50%;
    box-sizing: border-box;
    font-size: 20px;
    font-weight: 600;
    transform: translateY(-50%);
  }

  .warning {
    border: 2px solid var(--oc-red-9);
    padding: 8px 12px;
    margin-top: 4px;
    margin-bottom: 4px;
    line-height: 120%;
    max-width: 360px;
    border-radius: 4px;
    background-color: var(--oc-red-0);
    box-sizing: border-box;
  }
  .setting-warning {
    background: url('assets/warning.svg') var(--oc-red-0) 4px 4px no-repeat;
    background-size: 32px;
    padding-left: 42px;
  }
  .disabled .warning {
    border: 2px solid var(--oc-red-3);
  }
  .disabled .setting-warning {
    background-image: url('assets/warning-disabled.svg');
  }

  .small-multiples {
    padding: 32px 24px;
    border-top: 1px solid var(--oc-gray-6);
    box-sizing: border-box;
  }
    .updating {
      text-align: center;
      font-size: 24px;
      letter-spacing: 0.2em;
      text-transform: uppercase;
      margin-top: 20px;
      margin-bottom: 20px;
    }
    .all-countries {
      display: flex;
      width: 100%;
      box-sizing: border-box;
      flex-wrap: wrap;
      justify-content: space-around;
    }
      .country-wrapper {
        margin-bottom: 12px;
        text-align: center;
      }
        .country-info {
          font-size: 16px;
          font-weight: 600;
          letter-spacing: 0.05em;
          display: inline-block;
          color: var(--oc-gray-8);
          white-space: nowrap;
        }
        .country-info .ranking {
          color: var(--oc-gray-6);
        }
        .country-info .name {
          margin-right: 8px;
          white-space: nowrap;
        }
