.setting {
  margin: 20px auto 0;
}
  .setting-countries {
    margin-bottom: 12px;
    text-align: center;
  }
  .setting-title {
    font-weight: 600;
    font-size: 18px;
  }
  .setting-title .open {
    font-weight: 400;
    font-size: 16px;
    text-decoration: underline;
    margin-left: 12px;
    cursor: pointer;
  }
  .setting-title-sub {
    font-weight: 400;
    font-size: 16px;
    margin-left: 12px;
  }
    .country-id {
      display: inline-block;
      background-color: var(--oc-gray-3);
      padding: 4px 24px 2px 8px;
      font-size: 14px;
      line-height: 90%;
      margin-left: 8px;
      border-radius: 4px;
      background-image: url(assets/close.svg);
      background-position: calc(100% - 4px) center;
      background-size: 12px 12px;
      background-repeat: no-repeat;
      cursor: pointer;
    }
  .country-id.disabled {
    background-image: none;
    pointer-events: none;
    padding-right: 8px;
  }

  .setting-property {
    margin-bottom: 12px;
    display: flex;
    justify-content: center;
  }
    .setting-content {
      padding: 12px 12px;
      border-radius: 8px;
      background-color: var(--oc-gray-1);
      margin-right: 8px;
      margin-left: 8px;
    }
    .setting-content.countries {
      margin-top: 12px;
      margin-bottom: 12px;
      position: relative;
      text-align: left;
      line-height: 160%;
      display: inline-block;
    }
    .setting-content.apps {
      flex-flow: column;
      justify-content: center;
      line-height: 160%;
      display: flex;
    }
      .filter-list {
        margin-left: 8px;
      }
      .filter-list.disabled {
        color: var(--oc-gray-5);
      }
      .setting-content .close {
        position: absolute;
        right: 8px;
        top: 8px;
        width: 16px;
        height: 16px;
        background-image: url(assets/close.svg);
        background-size: 16px 16px;
        background-repeat: no-repeat;
        background-position: 0 0;
        cursor: pointer;
      }
      .family-list {
        margin-right: 8px;
      }

      .setting-content .animation {
        text-align: center;
        margin: 0 auto;
      }
        .animation .start {
          cursor: pointer;
          display: inline-block;
          margin-bottom: 8px;
          background: url('assets/play.svg') no-repeat 0 0;
          padding-left: 20px;
        }
        .animation .start:hover {
          background-image: url('assets/play-hover.svg');
        }
        .animation .start.pause {
          background: url('assets/pause.svg') no-repeat 0 0;
        }
        .animation .start.pause:hover {
          background-image: url('assets/pause-hover.svg');
        }
        .animation .start:hover {
          color: var(--oc-blue-4);
        }
        .animation .void, .animation .void:hover {
          color: var(--oc-red-9);
          background: none;
          cursor: default;
        }
      .setting-content .month {
        display: flex;
        flex-wrap: wrap;
      }
      .month.disabled {
        color: var(--oc-gray-6);
      }
      .setting-month {
        text-align: center;
      }
        .month-input {
          padding: 0 10px;
        }
        .month-text {
          margin-top: -12px;
          border-top: 2px solid var(--oc-gray-6);
          padding-top: 12px;
          font-size: 14px;
        }
        .disabled .month-text {
          border-top-color: transparent;
        }
        .year-text {
          margin-top: 4px;
          font-size: 14px;
          font-weight: 600;
        }
