.Country {
  margin: 28px 0 36px 0;
  position: relative;
}
  .base-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    pointer-events: none;
  }
  .base-calibrated {
    position: absolute;
    top: 0;
    right: 0;
    width: 24px;
    height: 24px;
    background: url('assets/warning.svg') no-repeat;
    z-index: 10;
    pointer-events: auto;
  }
  .calibration {
    position: absolute;
    pointer-events: none;
    right: 8px;
    top: 8px;
    border: 2px solid var(--oc-red-9);
    padding: 12px 16px;
    background-color: var(--oc-red-0);
    text-align: left;
    line-height: 120%;
    -webkit-box-shadow: 0px 2px 2px 2px rgba(33,37,41,0.2);
    -moz-box-shadow: 0px 2px 2px 2px rgba(33,37,41,0.2);
    box-shadow: 0px 2px 2px 2px rgba(33,37,41,0.2);
    border-radius: 4px;
  }

.circle-outer, .circle-inner {
  fill: var(--oc-gray-2);
  fill-opacity: 0;
  stroke: var(--oc-gray-6);
  stroke-width: 1;
  stroke-dasharray: 2.5 2;
}
.circle-outer:hover,
.circle-inner:hover {
  fill-opacity: 1;
}

.circle {
  fill-opacity: 0.7;
  stroke: var(--oc-gray-8);
  stroke-width: 0;
}
.circle.undim {
  stroke-width: 3;
  fill-opacity: 0.9;
}
.circle.dim {
  fill-opacity: 0.2;
}
  .set-fb {
    fill: var(--facebook);
  }
  .set-ig {
    fill: var(--instagram);
  }
  .set-wa {
    fill: var(--whatsapp);
  }
  .set-fb-ig {
    fill: var(--fb-ig);
  }
  .set-fb-wa {
    fill: var(--fb-wa);
  }
  .set-ig-wa {
    fill: var(--ig-wa);
  }
  .set-fb-ig-wa {
    fill: var(--fb-ig-wa);
  }
  .set-inner {
    fill: none;
    stroke: var(--oc-gray-6);
    stroke-width: 1;
    stroke-dasharray: 2.5 2;
  }

.labels {
  /* position: relative; */
  position: absolute;
  top: 0;
  left: 0;
}
  .number {
    position: absolute;
    height: 24px;
    width: 60px;
    font-weight: 400 !important;
  }
  .arrow {
    position: absolute;
    height: 24px;
    width: 24px;
    background-size: 24px 24px;
    background-repeat: no-repeat;
    padding-left: 22px;
    padding-top: 5px;
  }
  .arrow.odd-0 {
    margin-left: -12px;
    margin-top: -24px;
  }
  .arrow.odd-1 {
    margin-left: -6px;
    margin-top: 6px;
  }
  .arrow.odd-2 {
    margin-left: -16px;
    margin-top: 6px;
  }
  .arrow.even-0 {
    margin-left: -24px;
    margin-top: -12px;
  }
  .arrow.even-1 {
    margin-left: 2px;
    margin-top: -12px;
  }
  .arrow.family {
    background-image: url(assets/arrow-up.svg);
  }
  .arrow.up.fb {
    background-image: url(assets/arrow-up-fb.svg);
  }
  .arrow.up.ig {
    background-image: url(assets/arrow-up-ig.svg);
  }
  .arrow.up.wa {
    background-image: url(assets/arrow-up-wa.svg);
  }
  .arrow.down {
    background-image: url(assets/arrow-down.svg);
    color: var(--oc-gray-8);
  }
  .number.odd-0 {
    margin-top: -24px;
    margin-left: -30px;
  }
  .number.odd-1 {
    margin-top: 6px;
    margin-left: -10px;
  }
  .number.odd-2 {
    margin-top: 6px;
    margin-left: -50px;
  }
  .number.even-0 {
    margin-top: -12px;
    margin-left: -60px;
  }
  .number.even-1{
    margin-top: -12px;
    margin-left: 0px;
  }


.loading .circle {
  fill-opacity: 0.1;
}
.loading .changes {
  display: none;
}

.tooltip {
  position: absolute;
  text-align: left;
  color: white;
  overflow: visible;
  white-space: nowrap;
  pointer-events: none;
  line-height: 120%;
  background-color: var(--oc-gray-8);
  padding: 8px 12px;
  border-radius: 4px;
  z-index: 1;
}
.tooltip div:not(:first-child) {
  margin-top: 8px;
}
.tooltip.outline {
  color: var(--oc-gray-8);
  background: white;
  border: 2px solid var(--oc-gray-8);
  width: 100%;
  transform: translateY(-50%);
  top: 50%;
  left: 0;
  text-align: center;
  box-sizing: border-box;
  white-space: normal;
}
  .tooltip-value {
    font-size: 20px;
    font-weight: 600;
  }
  .tooltip-month {
    vertical-align: text-bottom;
  }
  .tooltip-value.calibrated {
    color: var(--oc-red-9);
  }
